.mealplan-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 0 1rem;
}
.mealplan-header {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #333;
}
.meal-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}
.meal-item {
  background: #ffffff;
  border-radius: 6px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.08);
  padding: 1rem;
}
.meal-item img {
  width: 100%;
  border-radius: 4px;
}
.meal-item button {
  margin-top: 0.75rem;
  padding: 0.5rem;
  background-color: #4cc9f0;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.meal-item button:hover {
  background-color: #3bb8d8;
}
